const auctionUrl = 'https://xinshu.service.letterbook.cn/auction'; // 

const socketUrl = 'wss://xinshu.service.letterbook.cn/ws'; // socket
// const auctionUrl = 'http://192.168.0.100:9300/auction' // 
// const socketUrl = 'ws://192.168.0.100:8088/ws' // socket
// 

function auctionRequest(url, data, type, method) {
    const zl_jwt = uni.getStorageSync('zl_jwt');
    const token = uni.getStorageSync('token');
    let promise = new Promise((resolve, reject) => {
        uni.showLoading({
            title: '',
            mask: true
        });
        uni.request({
            url: auctionUrl + url,
            method: method ? method : 'POST',
            data: data,
            header: {
                Authorization: zl_jwt,
                'content-type': type == 'form-data' ? 'application/form-data' : 'application/json;charset=UTF-8'
            },
            success: (res) => {
                if (res.statusCode == 200) {
                    if (res.data.code == 403 && !token) {
                        console.log(111);
                        uni.navigateTo({
                            url: '/pages/shopHome/home/home'
                        });
                    }

                    resolve(res);
                } else {
                    reject(res.data);
                }
            },
            fail: () => {
                uni.showToast({
                    title: '',
                    icon: 'none'
                });
                reject('');
            },
            complete: () => {
                uni.hideLoading();
            }
        });
    });
    return promise;
}

module.exports = {
    socketUrl: socketUrl,
    auctionRequest: auctionRequest
};
